$cfg: (
    'line-thickness': 2px,
    'line-margin-top': 0.75em,
    'line-color': rgba($primary, 0.5),
    'color': $gray-900,
    'min-height': 312px
);

.strikethrough-jumbo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  min-height: map-get($cfg, 'min-height');

  .strikethrough {
    display: inline-block;
    position: relative;
    overflow: hidden;
    font-weight: 400;
    font-size: 1em;
    opacity: 0.8;
    @include smooth();
  }
  .strikethrough s {
    position: absolute;
    display: block;
    width: 100%;
    height: map-get($cfg, 'line-thickness');
    margin-top: map-get($cfg, 'line-margin-top');
    background: map-get($cfg, 'line-color');
    transform-origin: center left;
  }

  .title {
    margin-top: 30px;
  }
  
}

@include media-breakpoint-down(md) {
  .strikethrough-jumbo {
    padding-top: 140px;
    padding-bottom: 70px;
    min-height: 520px;
  }
}